@import url(https://fonts.googleapis.com/css?family=Catamaran:300,400,700&display=swap);
html,body{font-family:"Catamaran", "Helvetica Neue", Helvetica, Arial, sans-serif;font-size:14px;color:#000;margin:0;padding:0;background-color:#eee;text-align:left;box-sizing:border-box;overflow-x:hidden}

.Navigation{position:relative;background-color:#0b102bc0;position:fixed;top:0;left:0;right:0;box-shadow:0 3px 6px rgba(128,128,128,0.4);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);z-index:1}.Navigation .nav_items{display:flex;justify-content:space-around;align-items:center;width:80%;margin:0 auto}.Navigation ul{list-style:none;display:flex;margin:10px 0;padding:0;align-items:center}.Navigation li{padding:10px 5px;margin:0 20px;text-align:center}.Navigation a{position:relative;color:#bebebe;text-decoration:none;font-size:1.5rem;transition:0.4s}.Navigation a::after{content:"";display:block;position:absolute;bottom:10%;left:50%;height:1px;width:0;background:transparent;transition:0.4s;background:#bebebe}.Navigation a:hover{color:white}.Navigation a:hover::after{width:100%;left:0;background:white}.Navigation .active{color:white}.Navigation .cart_btn{background-color:#519cda;border-radius:50px;border:2px solid rgba(255,255,255,0.185);padding:5px 15px;font-family:"Catamaran", sans-serif;transition:0.3s;cursor:pointer}.Navigation .cart_btn:hover{border:2px solid rgba(255,255,255,0.6);background-color:#5aa9e9}.Navigation .cart_btn a{text-align:center;font-weight:bold;font-size:1.2rem;color:white}.Navigation .cart_btn a::after{display:none}

.product{width:25%;background:white;margin:0 1% 2%;text-align:center;flex:1 0 23%;border-radius:10px;overflow:hidden;max-width:260px;box-shadow:3px 3px 10px rgba(128,128,128,0.4);transition:0.4s;cursor:pointer}.product img{display:block;width:85%;height:auto;margin:0 auto;transition:0.4s;padding-top:10px}.product:hover{box-shadow:3px 3px 10px #0b102b69}.product:hover img{transform:scale(1.1)}.product p.price{display:inline-block;font-size:24px;line-height:28px;color:#00a1b4;padding:3px 0px;margin:10px auto}.product h3{font-size:1.4rem;margin:5px auto}.product button{font-family:"Catamaran", sans-serif;font-weight:bold;padding:5px;border-radius:10px;border:none;margin:15px auto;background-color:#519cda;color:white;transition-duration:0.3s;cursor:pointer;overflow:hidden;position:relative;outline:none}.product button::after{content:"";background:#519cda;display:block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);padding-top:300%;padding-left:350%;margin-left:-70px !important;box-shadow:0 0 5px 5px rgba(146,217,251,0.85);opacity:0;transition:all 0.6s}.product button:active:after{padding:0;margin:0;opacity:1;transition:0s}.product button:active{transform:translateY(5px)}.product button:hover{background-color:#1881d8}

.Catalog{display:flex;min-height:100vh}.Catalog .column-left{position:relative;width:25%;height:100%}.Catalog .column-right{width:75%}.Catalog .products{display:flex;flex-wrap:wrap}.Catalog h2{text-align:center;margin:10px auto}

.Filter{top:0;left:0;padding:10px;background-color:white;border-radius:10px;margin-right:10px;box-shadow:3px 3px 10px rgba(128,128,128,0.4)}.Filter h3{font-size:2rem;text-transform:uppercase;text-align:center;margin:15px auto}.Filter h4{font-size:1.5rem;margin-bottom:10px}.Filter .clear{position:absolute;top:0;right:0;color:white;font-weight:bold;cursor:pointer;background-color:#e00;padding:10px;border-radius:10px;transition:0.3s;border:none}.Filter .clear:hover{background-color:red}.Filter .search-bar{position:relative;margin-top:40px}.Filter .search-input{font-family:"Catamaran", sans-serif;padding:5px;width:calc(100% - 15px);font-size:1.3rem;border-radius:10px;border:1px solid gray;outline:none}

.Radio{margin-left:10px}.Radio .container{display:block;position:relative;padding-left:35px;margin-bottom:12px;cursor:pointer;font-size:15px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.Radio .container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.Radio .checkmark{position:absolute;top:0;left:0;height:20px;width:20px;background-color:#eee;border-radius:50%}.Radio .container:hover input ~ .checkmark{background-color:#ccc}.Radio .container input:checked ~ .checkmark{background-color:#2196f3}.Radio .checkmark:after{content:"";position:absolute;display:none}.Radio .container input:checked ~ .checkmark:after{display:block}.Radio .container .checkmark:after{top:7px;left:7px;width:7px;height:7px;border-radius:50%;background:white}

.AboutPage .about{font-size:1.5rem;line-height:1.9;text-align:justify}.AboutPage .about .top{display:flex;align-items:center}.AboutPage .about .top p{padding-right:40px}.AboutPage.container{background:white;border-radius:10px;box-shadow:3px 3px 10px rgba(128,128,128,0.4);padding:0 40px 20px}

.ContactPage.container{background:white;border-radius:10px;box-shadow:3px 3px 10px rgba(128,128,128,0.4);max-width:800px}.ContactPage.container h3{text-align:center;font-weight:normal;color:gray}h2.header-big{text-align:center}.form_container{max-width:650px;padding:50px 0 100px;margin:0 auto}.input{width:100%;margin-bottom:20px;position:relative}.input input{position:relative;font-family:"Catamaran", "Helvetica Neue", Helvetica, Arial, sans-serif;font-size:1.5rem;background-color:#eee;width:calc(100% - 70px);height:60px;border-radius:30px;border:none;padding:0 35px}form{display:flex;flex-direction:column;align-items:center}textarea{font-family:"Catamaran", "Helvetica Neue", Helvetica, Arial, sans-serif;box-sizing:border-box;width:100%;min-height:170px;border-radius:30px;border:none;padding:20px 35px;font-size:1.5rem;resize:vertical;background-color:#eee}.send_btn{font-family:"Catamaran", "Helvetica Neue", Helvetica, Arial, sans-serif;margin:0 auto;width:170px;padding:15px 0;border:none;background-color:#519cda;border-radius:50px;cursor:pointer;color:white;font-size:1.5rem;text-transform:uppercase;font-weight:bold;transition:0.5s}.send_btn:hover{width:190px;background-color:#0363b2}.alert-validate:hover::after{background-color:red}.alert-validate::after{content:"!";position:absolute;top:50%;right:25px;transform:translateY(-50%);font-family:"Catamaran", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:bolder;font-size:1.5rem;color:white;background-color:#d00;display:block;width:25px;height:25px;text-align:center;border-radius:50%;line-height:30px;transition:0.3s;z-index:100}.alert-validate::before{content:attr(validator-message);position:absolute;max-width:70%;min-width:70px;z-index:50;border:1px solid red;border-radius:20px;padding:6px 35px 6px 10px;top:50%;transform:translateY(-50%);right:20px;font-family:"Catamaran", "Helvetica Neue", Helvetica, Arial, sans-serif;color:red;font-size:13px;line-height:20px;text-align:left;font-weight:bold}

.empty_cart_container{position:relative}.empty_cart_container h3{font-size:3rem;text-align:center}.empty_cart_container .cart_icon{font-size:400px;color:rgba(0,0,0,0.2);text-align:center;transform:translateX(-2%)}

.Footer{background-color:#2a2a2a;color:white;padding:10px;margin-top:40px}

.App{display:flex;flex-direction:column;min-height:100vh;margin:0 auto;background-color:#eee;width:100%}.main_container{margin-top:74px}.header-big{font-size:3rem;text-transform:uppercase;padding:15px 0}.header-small{font-size:2.2rem;text-transform:uppercase;padding:5px 0}.container{position:relative;max-width:1200px;margin:0 auto}

